body {
	font-family: $fMon;
}

.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);

	height: 1px !important;
	width: 1px !important;
	padding: 0 !important;
	border: 0 !important;
	overflow: hidden;
}

.common-link {
	display: inline-block;

	background-color: #998675;
	box-shadow: inset 0 -3px 0 #736357;
	border-radius: 3px;
	color: #fff;
	padding: 15px 35px;
	transition: 0.2s;

	font-family: "Raleway-Extra";
	font-size: 14px;
	line-height: 23px;
	text-transform: uppercase;

	&:hover {
		background-color: #aa8462;
	}
	&:focus {
		background-color: #aa8462;
	}
	&:active {
		background-color: #b49b85;
	}
}

.logo {
	display: flex;
	align-items: center;

	span {
		color: #ffffff;

		font-family: $fRal;
		font-size: 34px;
		line-height: 26px;
		letter-spacing: -1.36px;
	}
}

.title {
	color: #555555;

	font-family: $fRal;
	font-weight: 500;
	font-size: 30px;
	text-align: center;
}

.color-blue {
	color: #00bff3;
}

.color-white {
	color: #959595;
}
