// modal
/*  Объект для модального окна, чтобы страница не прокручивалась вверх  */
.fixed {
	position: fixed;
	top: 0;
	left: 0;
	opacity: 0;
}
/* ----- */
.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.726);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	backface-visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.7s ease;
	z-index: 9999;
	&.open {
		opacity: 1;
		visibility: visible;
	}
	.popup.open &__content {
		opacity: 1;
		transform: translate(0px, 0px);
	}

	&__wrapper {
		min-height: 90vh;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 30px 10px;
	}

	&__content {
		background-color: #fff;
		color: #000;
		max-width: 600px;
		padding: 30px;
		display: flex;
		flex-direction: column;
		position: relative;
		transition: all 0.7s ease;
		opacity: 0;
		transform: translate(0px, -100%);
		form {
			display: flex;
			flex-direction: column;
		}
		img.popup__image {
			width: 100%;
		}
	}

	&__close {
		align-self: flex-end;
		position: absolute;
		top: 5px;
		right: 5px;
		transition: all 0.2s ease-in;
		&:hover {
			transform: scale(1.2);
		}
	}
	&__title {
		text-align: center;
		font-size: 28px;
		margin-bottom: 20px;
		font-weight: 500;
	}

	&__close-image {
		width: 15px;
	}

	&__text {
		text-align: center;
	}
}
.form {
	position: relative;
	width: 100%;
	margin-bottom: 30px;
	&__search {
		background: linear-gradient(to right, #211b19 0%, #4e3427 100%);
		padding-left: 10px;
		height: 40px;
		color: #fff;
		border: 1px solid rgb(0, 0, 0);
		border-radius: 5px;

		font-family: $fMon;

		transition: all 0.3s ease;

		&::placeholder {
			font-size: 16px;
		}
		&:hover {
			-webkit-box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
			-moz-box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
			box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
		}
		&:focus {
			-webkit-box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
			-moz-box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
			box-shadow: 1px 1px 20px 0px rgba(0, 0, 0, 0.75);
		}
	}

	&__submit {
		position: absolute;
		top: 50%;
		right: 10px;
		flex: 0 0 auto;
		width: 30px;
		height: 30px;
		background: url(../img/header/search.svg) center center no-repeat;
		cursor: pointer;
		transition: all 0.2s ease;
		transform: translateY(-50%);
		&:hover {
			transform: translateY(-50%) scale(1.2);
		}
	}
}

.shoping {
	&__rows {
		display: flex;
		flex-direction: column;
	}

	&__row {
		display: flex;
		justify-content: space-between;
	}

	&__total {
		font-size: 24px;
		padding: 5px 0;
	}
	&__price {
		font-size: 24px;
		font-weight: 700;
	}

	&__checkout {
		display: inline-block;
		padding: 5px 0;
		color: blue;

		border-radius: 5px;

		font-size: 24px;

		transition: all 0.2s ease;
		&:hover {
			transform: scale(1.1);
		}
	}
}
.shoping-block {
	position: relative;

	display: flex;
	justify-content: space-between;
	margin-bottom: 60px;

	transition: all 0.2s ease;
	&:hover {
		transform: scale(1.05);
	}
	&::after {
		content: "";
		position: absolute;
		bottom: -30px;
		left: 0;
		width: 100%;
		height: 2px;
		background-color: rgb(177, 177, 177);
	}
	&__image {
		flex: 0 0 30%;
		margin-right: 10px;
		img {
			width: 100%;
			height: 100px;
			object-fit: cover;
		}
	}

	&__name {
		color: #000;

		margin-right: 20px;

		text-align: justify;
	}

	&__price {
		color: black;

		font-size: 24px;
		font-weight: 700;
	}
	@media (max-width: $md3+px) {
		&__price {
			font-size: 18px;
		}
	}
	@media (max-width: $md4+px) {
		& {
			flex-direction: column;
		}
		&__image {
			margin-right: 0;
			margin-bottom: 20px;
			img {
				height: 150px;
			}
		}
		&__name {
			margin-right: 0px;
			margin-bottom: 20px;
		}
		&__price {
			align-self: flex-end;
		}
	}
}
