.mobile {
	&__container {
		padding-top: 180px;
		padding-bottom: 180px;
		display: flex;
		justify-content: flex-end;
	}
	&__wrapper {
		max-width: 657px;
	}
	&__title {
		margin-bottom: 40px;
		color: #555555;

		font-family: $fRal;
		font-size: 30px;
		font-weight: 600;
		text-align: left;
	}

	&__text {
		color: #8c8c8c;
		margin-bottom: 45px;

		font-family: $fMon;
		font-size: 16px;
		font-weight: 300;
		line-height: 48 / 30 * 100%;
		text-align: justify;
	}

	&__list {
		color: #8c8c8c;

		font-family: $fMon;
		font-size: 16px;
		font-weight: 300;
		line-height: 32px;
		text-align: left;
		li {
			position: relative;
			margin-left: 100px;

			transition: all 0.3s ease;
			cursor: pointer;
			&:hover {
				color: #e0a029;
			}
			&:focus {
				color: #e0a029;
			}
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: -51px;

				height: 32px;
				width: 21px;
				background: url(../img/list-icon.svg) center center no-repeat;
				transform: rotate(-90deg);

				line-height: 21px;
			}
		}
	}
	@media (min-width: $md2+px) {
		&__container {
			background: url(../img/mobile-bg.png) left bottom no-repeat;
			background: none;
		}
	}
	@media (max-width: $md2+px) {
		&__container {
			padding-top: 100px;
			padding-bottom: 100px;
			background: none;
			justify-content: center;
		}
		&__list {
			li {
				margin-left: 51px;
			}
		}
	}
	@media (max-width: $md3+px) {
		&__container {
			padding-top: 50px;
			padding-bottom: 50px;
		}

		&__list {
			li {
				margin-left: 30px;
				&::after {
					left: -31px;
				}
			}
		}
	}
}
