.service {
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		padding-top: 80px;
		padding-bottom: 50px;
		justify-content: space-between;
	}

	&__block {
		flex: 0 0 25%;
		min-height: 280px;
		display: flex;
		position: relative;
		flex-direction: column;
		text-align: center;
		padding: 10px;
		margin-bottom: 30px;
		&::after {
			content: "";
			position: absolute;
			bottom: 0px;
			left: 50%;
			background-color: #f1eee9;
			width: 70px;
			height: 3px;
			transform: translate(-50%, 0);
			transition: background-color 0.3s ease;
		}
		&:hover {
			.service__icon svg,
			.service__title {
				color: #c7b299;
				fill: #c7b299;
			}
			&::after {
				background-color: #c7b299;
			}
		}
	}

	&__icon svg,
	&__title,
	&__text::after {
		transition: all 0.2s ease;
	}
	&__icon {
		@include adaptive-value("margin-bottom", 30, 10, 1);
		height: 50px;
		svg {
			fill: #555;
		}
	}

	&__title {
		color: #555555;
		font-family: "Raleway", sans-serif;
		font-weight: 600;
		font-size: 22px;
		@include adaptive-value("margin-bottom", 20, 10, 1);
	}

	&__text {
		position: relative;
		font-family: "Montserrat", sans-serif;
		color: #8c8c8c;
		font-size: 14px;
		font-weight: 300;
		line-height: 24px;
	}

	@media (max-width: 876px) {
		&__wrapper {
			padding-top: 50px;
			padding-bottom: 5px;
		}
		&__block {
			flex: 0 0 50%;
			min-height: 220px;
			margin-bottom: 40px;
		}
	}
	@media (max-width: 555px) {
		&__wrapper {
			padding-top: 40px;
		}
		&__block {
			flex: 0 0 100%;
			min-height: 190px;
		}
	}
	@media (orientation: landscape) and (max-width: 1023px) and (max-height: 767px) {
		&__wrapper {
			padding-top: 50px;
		}
		&__block {
			flex: 0 0 50%;
			min-height: 220px;
		}
	}
}
