// Переменные
$minWidth: 320;
$maxWidth: 1920; // Разрешение макета
$maxWidthContainer: 1170; // Разрешение контейнера
$md1: $maxWidthContainer + 12; //desktop
$md2: 991.98; // Tablet
$md3: 767.98; // Mobile #1
$md4: 479.98; // Mobile #2

$fMon: "Montserrat", sans-serif;
