.partners {
	background: url(../img/partners/partners-bg.jpg) center / cover no-repeat;
	padding: 140px 0;
	position: relative;
	z-index: 3;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;

		width: 100%;
		height: 100%;
		background-color: rgba(17, 17, 17, 0.7);
	}
	&__container {
		position: relative;
		z-index: 5;
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 1110px;
		margin: 0 auto;
	}

	&__image {
		flex: 0 0 25%;
		text-align: center;
		img {
			max-width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	@media (max-height: $md4+px) and (orientation: landscape) {
		&__container {
			padding-right: 40px;

			padding-left: 40px;
		}
	}
	@media (max-width: $md2+px) {
		padding: 70px 0;
		&__image {
			flex: 0 0 20%;
			text-align: center;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	@media (max-width: $md3+px) {
		padding-top: 30px;
		padding-bottom: 0;
		&__container {
			flex-wrap: wrap;
		}
		&__image {
			flex: 0 0 40%;
			margin-bottom: 40px;
			img {
				width: 40%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__image {
			img {
				width: 60%;
			}
		}
	}
}
