.realization {
	background-color: #fbfaf8;
	@include adaptive-value("padding-top", 80, 30, 1);
	&__title {
		@include adaptive-value("margin-bottom", 24, 10, 1);
	}

	&__text {
		max-width: 757px;
		margin: 0 auto;
		color: #8c8c8c;
		font-family: $fMon;
		font-size: 16px;
		font-weight: 300;
		line-height: 28px;
		text-align: center;
		@include adaptive-value("margin-bottom", 40, 20, 1);
	}
	&__image {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	@media (max-width: $md2+px) {
		&__text {
			font-size: 14px;
		}
	}
}

.purchase {
	background: linear-gradient(to right, #211b19 0, #4e3427 100%);
	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include adaptive-value("padding-bottom", 50, 10, 1);
		@include adaptive-value("padding-top", 50, 10, 1);
	}
	&__column:first-child {
		margin-right: 20px;
	}

	&__title {
		color: #ffffff;
		font-family: $fRal;
		@include adaptive-value("font-size", 28, 20, 1);
		font-size: 28px;
		span {
			color: #c7b299;
		}
		margin-bottom: 8px;
	}

	&__text {
		color: #615855;
		font-family: $fMon;
		@include adaptive-value("font-size", 18, 10, 1);
		font-size: 18px;
		font-weight: 300;
	}
	@media (max-width: 837px) {
		&__link {
			padding: 10px 20px;
			font-size: 12px;
		}
		&__container {
			display: flex;
			flex-direction: column;
			text-align: center;
		}

		&__text {
			@include adaptive-value("margin-bottom", 20, 10, 1);
		}
		&__column:first-child {
			margin-right: 0px;
		}
	}
	@media (orientation: landscape) and (max-width: 1024px) and (max-height: 767px) {
		&__container {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 10px 45px;
		}

		&__link {
			padding: 5px 15px;
			font-size: 12px;
		}
	}
}
