.footer {
	background-color: #191919;

	&__container {
		max-width: 1200px;
		padding: 80px 15px;
	}
	&__wrapper {
		display: flex;
		justify-content: space-between;
		margin: 0 -15px;
	}
	&__column {
		flex: 0 0 25%;
		padding: 0 15px;

		margin-bottom: 35px;
	}

	&__logo {
		margin-bottom: 25px;
		cursor: default;
		span {
			position: relative;

			padding-left: 60px;
		}
		span::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;

			background: url(../img/logo.svg) center center no-repeat;
			width: 64px;
			height: 56px;
			transform: translateY(-25%);
		}
	}

	&__text {
		color: #555555;
		margin-bottom: 30px;

		text-align: justify;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;
		line-height: 24px;
	}

	&__link-read-more {
		position: relative;

		display: inline-block;
		padding-right: 15px;
		color: #d1d1d1;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;

		transition: transform 0.3s ease;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;

			width: 5px;
			height: 8px;
			background: url(../img/footer/arrow.svg) center center no-repeat;
			transform: translateY(-50%);
		}
		&:hover {
			transform: scale(1.1);
		}
		&:focus {
			transform: scale(1.1);
		}
	}

	&__subtitle {
		color: #ffffff;
		margin-bottom: 25px;

		text-align: justify;
		font-family: $fRal;
		font-size: 24px;
		font-weight: 700;
	}

	&__link {
		position: relative;

		display: inline-block;
		color: #555555;

		text-align: justify;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;
		line-height: 24px;

		transition: color 0.3s ease;
		&:hover {
			color: #c7b299;
		}
		&:focus {
			color: #c7b299;
		}
		&:hover .footer__date {
			color: #ffffff;
		}
		&:focus .footer__date {
			color: #ffffff;
		}
		&:not(:last-child) {
			margin-bottom: 40px;
			&::before {
				content: "";
				position: absolute;
				left: 0;
				bottom: -20px;

				width: 100%;
				height: 1px;
				background-color: #222;
			}
		}
	}

	&__date {
		color: #363636;

		font-family: $fMon;
		font-size: 12px;
		font-weight: 300;

		transition: color 0.3s ease;
	}
	@media (max-height: $md4+px) and (orientation: landscape) {
		&__container {
			padding: 80px 40px;
		}
	}
	@media (max-width: $md2+px) {
		&__container {
			padding-top: 80px;
			padding-bottom: 30px;
		}
		&__wrapper {
			flex-wrap: wrap;
		}
		&__column {
			flex: 0 0 50%;
		}
	}
	@media (max-width: $md3+px) {
		&__container {
			padding-top: 50px;
			padding-bottom: 00px;
		}
	}
	@media (max-width: 600px) {
		&__container {
			padding-top: 50px;
			padding-bottom: 0px;
		}
		&__wrapper {
			flex-wrap: wrap;
		}
		&__column {
			flex: 0 0 100%;
			margin-bottom: 35px;
		}
		&__acordion {
			visibility: hidden;
			overflow: scroll;
			height: 0px;
			transition: all 0.3s ease;
		}
		.acordion-triger {
			position: relative;

			padding-right: 60px;
			margin-bottom: 0;
			transition: all 0.3s ease;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				right: 0;

				width: 16px;
				height: 16px;
				background: url(../img/sort-down.svg) center / cover no-repeat;
				transform: translateY(25%);
				transition: all 0.3s ease;
			}
		}
		.acordion--active &__acordion {
			visibility: visible;
			height: 100%;
		}
		.acordion--active .acordion-triger {
			margin-bottom: 25px;
			transition: all 0.3s ease;
			&::after {
				transform: rotate(180deg);
			}
		}
	}
}

.widget {
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -5px;
	}

	&__image-link {
		flex: 0 0 50%;
		padding: 0 5px;
		display: inline-block;
		margin-bottom: 10px;
		transition: transform 0.2s ease;
		&:hover {
			transform: scale(1.05);
		}
		&:focus {
			transform: scale(1.05);
		}
	}

	&__image {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.credit {
	background-color: #111111;
	padding: 45px 0;
	&__container {
		display: flex;
		justify-content: space-between;
		max-width: 1200px;
		padding: 0 15px;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		position: relative;

		color: #555555;

		font-family: $fMon;
		font-size: 13px;
		font-weight: 300;
		line-height: 20px;
		&:not(:last-child) {
			margin-right: 25px;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				right: -12.5px;

				width: 1px;
				height: 100%;
				background-color: #222;
			}
		}
	}

	&__link {
		color: #555555;

		font-family: $fMon;
		font-size: 13px;
		font-weight: 300;
		line-height: 20px;
		transition: color 0.3s ease;
		&:hover {
			color: #c7b299;
		}
		&:focus {
			color: #c7b299;
		}
	}
	@media (max-width: $md2+px) {
		& {
			padding: 20px 0;
		}
		&__container {
			flex-direction: column;
			align-items: center;
		}
		&__list {
			align-items: center;
			justify-content: center;
		}
	}
	@media (max-width: $md4+px) {
		&__container {
			flex-direction: column;
			align-items: flex-start;
		}
		&__list {
			flex-direction: column;
			align-items: flex-start;
			justify-content: center;
		}
		&__item {
			&:not(:last-child) {
				margin: 0;
				&::after {
					display: none;
				}
			}
		}
	}
}
