.slider__promo {
  color: #ffffff;
  margin-bottom: 14px;
  font-family: "Raleway", sans-serif;
  font-size: calc(20px + 16 * ((100vw - 320px) / 1600));
  line-height: 1.2;
  font-weight: 500;
  max-height: 150px;
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.slider__promo::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.slider__title {
  color: #ffffff;
  margin-bottom: 20px;
  max-height: 150px;
  overflow-y: auto;
  line-height: normal;
  font-family: "Raleway-Extra";
  font-size: calc(26px + 26 * ((100vw - 320px) / 1600));
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.slider__title::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.slider__text {
  font-size: calc(14px + 2 * ((100vw - 320px) / 1600));
  color: #ffffff;
  margin-bottom: 50px;
  max-height: 150px;
  overflow-y: auto;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  line-height: 24px;
  text-align: center;
  -ms-overflow-style: none;
  /* IE 10+ */
