.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.726);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  backface-visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.7s ease;
  z-index: 9999;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__content {
  opacity: 1;
  transform: translate(0px, 0px);
}

.popup__wrapper {
