@media (max-width: 837px) {
  .header__wrapper {
    padding-top: 0;
    padding: 0;
  }

  .header__logo {
    flex: 0 0 30px;
  }

  .header__logo img {
    height: 30px;
  }

  .header__logo span {
    font-size: 28px;
  }

  .header__body {
    height: 50px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
    position: relative;
  }

  .header__burger span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .header__burger:before,
  .header__burger:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }

  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, #211b19 0%, #4e3427 100%);
    padding: 20px 10px 20px 10px;
    transition: all 0.3s ease 0s;
    overflow: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .header__menu.active {
    top: 0;
  }

  .header__list {
    flex: 1 1 100%;
    display: block;
    max-height: 100%;
    text-align: center;
    margin-right: 0;
  }

  .header .bug-link span {
    display: inline-block;
  }

  .header .bug-link img {
    display: none;
  }

  .header__list li {
    margin: 0 0 20px 0px;
  }

  .header__link {
    font-size: 28px;
  }

  .header .sublist {
    padding-top: 20px;
    transform: translate(-15px, 0);
  }

  .header__icon-list {
    display: none;
  }

  .slider__wrapper {
    padding: 0 50px;
  }

  .slider__text {
    margin-bottom: 20px;
  }

  .swiper-pagination {
    position: absolute;
  }

  .purchase__link {
