.header {
	background-color: black;
	background: url(../img/header/bckgrnd.jpg) center/cover no-repeat;
	height: 100%;
	position: relative;
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(17, 17, 17, 0.7);
	}
	&__wrapper {
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: transparent;
			z-index: 2;
		}
		position: fixed;
		width: 100%;
		padding-top: 40px;
		transition: padding-top 0.3s ease;
		left: 0;
		z-index: 50;
	}
	&__wrapper._active {
		padding-top: 5px;
		padding-bottom: 5px;
		background: linear-gradient(to right, #211b19 0%, #4e3427 100%);
	}

	&__body {
		z-index: 5;
		max-width: 1140px;
		margin: 0 auto;
		padding: 0 15px;
		position: relative;
		display: flex;
		justify-content: space-between;
		height: 60px;
		align-items: center;
	}
	&__burger {
		display: none;
		cursor: pointer;
	}
	&__menu {
		height: 56px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__logo {
		margin-right: 20px;
	}
	&__list {
		display: flex;
		margin-top: 7px;
		margin-right: 60px;
		&:last-child {
			margin-right: 0;
		}
		li {
			margin-right: 30px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&__icon-list {
		margin-top: 0;
		li {
			display: flex;
			justify-content: flex-end;
		}
	}

	&__link {
		color: #ffffff;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;
		transition: color 0.2s ease-in;
		&:hover {
			color: $cBrown;
		}
		&:focus {
			color: $cBrown;
		}
	}
	.bug-link {
		display: inline-block;
		transition: all 0.2s ease;
		&:hover {
			transform: scale(1.2);
		}
		&:focus {
			transform: scale(1.2);
		}
		span {
			display: none;
		}
	}
	.search-link {
		display: inline-block;
		transition: all 0.2s ease;
		&:hover {
			transform: scale(1.2);
		}
		&:focus {
			transform: scale(1.2);
		}
		span {
			display: none;
		}
	}

	@media (max-width: 1023px) {
		&__logo {
			margin-right: 0px;
		}
		&__list {
			flex: 1 1 80%;
			margin-top: 5px;
			margin-right: 0;
			flex-wrap: wrap;
			justify-content: space-around;
			li {
				flex: 1 1 25%;
				margin-right: 0px;
				&:first-child {
					margin-bottom: 10px;
				}
			}
		}
		&__icon-list {
			margin-top: 10px;
			flex: 1 1 20%;
			li {
				display: flex;
				justify-content: flex-end;
			}
		}
	}
	@media (max-width: 1023px) {
		&__wrapper {
			top: 0px;
			padding: 0 10px;
		}
	}
	@media (orientation: landscape) and (max-width: 1023px) and (max-height: 480px) {
		&__body {
			padding-left: 30px;
			padding-right: 30px;
		}
	}

	@media (max-width: 837px) {
		&__wrapper {
			padding-top: 0;
			padding: 0;
		}

		&__logo {
			flex: 0 0 30px;
			img {
				height: 30px;
			}
			span {
				font-size: 28px;
			}
		}
		&__body {
			height: 50px;
		}
		&__burger {
			display: block;
			position: relative;
			width: 30px;
			height: 20px;
			z-index: 3;
			position: relative;
		}
		&__burger span {
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: #fff;
			left: 0;
			top: 9px;
			transition: all 0.3s ease 0s;
		}
		&__burger:before,
		&__burger:after {
			content: "";
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: #fff;
			left: 0;
			transition: all 0.3s ease 0s;
		}
		&__burger:before {
			top: 0;
		}
		&__burger:after {
			bottom: 0;
		}
		&__burger.active span {
			transform: scale(0);
		}
		&__burger.active:before {
			transform: rotate(45deg);
			top: 9px;
		}
		&__burger.active:after {
			transform: rotate(-45deg);
			bottom: 9px;
		}
		&__menu {
			position: fixed;
			top: -100%;
			left: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(to right, #211b19 0%, #4e3427 100%);
			padding: 20px 10px 20px 10px;
			transition: all 0.3s ease 0s;
			overflow: auto;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&__menu.active {
			top: 0;
		}
		&__list {
			flex: 1 1 100%;
			display: block;
			max-height: 100%;
			text-align: center;
			margin-right: 0;
		}
		.bug-link {
			span {
				display: inline-block;
			}
			img {
				display: none;
			}
		}

		&__list li {
			margin: 0 0 20px 0px;
		}
		&__link {
			font-size: 28px;
		}
		.sublist {
			padding-top: 20px;
			transform: translate(-15px, 0);
		}
		&__icon-list {
			display: none;
		}
	}
}

// Swiper
.swiper-container {
	max-width: 1600px;
	height: 100%;
}
.swiper-wrapepr {
	height: 100%;
}
.swiper-slide {
	text-align: center;
	font-size: 18px;
	color: #fff;
	// Center slide text vertically
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
}
.slider {
	&__wrapper {
		margin: 0 auto;
		margin-top: 50px;
		max-width: 590px;
	}

	&__promo {
		color: #ffffff;
		margin-bottom: 14px;

		font-family: $fRal;
		@include adaptive-value("font-size", 36, 20, 0);
		line-height: 1.2;
		font-weight: 500;
		max-height: 150px;
		overflow-y: auto;
		&::-webkit-scrollbar {
			// chrome based
			width: 0px; // ширина scrollbar'a
			background: transparent; // опционально
		}
		-ms-overflow-style: none; // IE 10+
		scrollbar-width: none; // Firefox
	}

	&__title {
		color: #ffffff;
		margin-bottom: 20px;
		max-height: 150px;
		overflow-y: auto;

		line-height: normal;
		font-family: "Raleway-Extra";
		@include adaptive-value("font-size", 52, 26, 0);

		&::-webkit-scrollbar {
			// chrome based
			width: 0px; // ширина scrollbar'a
			background: transparent; // опционально
		}
		-ms-overflow-style: none; // IE 10+
		scrollbar-width: none; // Firefox
	}

	&__text {
		@include adaptive-value("font-size", 16, 14, 0);

		color: #ffffff;
		margin-bottom: 50px;
		max-height: 150px;
		overflow-y: auto;

		font-family: $fMon;
		font-weight: 300;
		line-height: 24px;
		text-align: center;
		&::-webkit-scrollbar {
			/* chrome based */
			width: 0px; /* ширина scrollbar'a */
			background: transparent; /* опционально */
		}
		-ms-overflow-style: none; /* IE 10+ */
		scrollbar-width: none; /* Firefox */
	}
}

.swiper-button-prev {
	color: #fff;
	opacity: 0.5;
	transition: all 0.2s ease-in;
}
.swiper-button-prev:hover {
	color: #d3c1ad;
}

.swiper-button-next {
	color: #fff;
	opacity: 0.5;
	transition: all 0.3s ease;
}
.swiper-button-next:hover {
	color: #d3c1ad;
}
.swiper-pagination {
	display: flex;
	align-items: center;
	justify-content: center;
}
.swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
	bottom: 20px;
}
.swiper-pagination-bullet {
	width: 9px;
	height: 9px;
	border: 2px solid #fff;
	background-color: #fff;

	opacity: 0.5;
	transition: all 0.2s ease-in;
}
.swiper-pagination-bullet:hover {
	width: 9px;
	height: 9px;
	border: 2px solid #d3c1ad;

	background-color: transparent;
	opacity: 0.5;
}
.swiper-pagination-bullet-active {
	width: 15px;
	height: 15px;
	border: 2px solid #d3c1ad;
	background-color: transparent;
	transition: none;
}
.swiper-pagination-bullet-active:hover {
	width: 15px;
	height: 15px;
	border: 2px solid #d3c1ad;
	background-color: transparent;
	transition: none;
	opacity: 0.5;
}

@media (orientation: landscape) and (max-width: 1024px) {
	.slider__text {
		margin-bottom: 20px;
	}
	.swiper-button-next,
	.swiper-container-rtl .swiper-button-prev {
		right: 40px;
	}
	.swiper-button-prev,
	.swiper-container-rtl .swiper-button-next {
		left: 40px;
	}
	.swiper-header {
		padding: 20px;
	}
}

@media (max-width: 837px) {
	.slider__wrapper {
		padding: 0 50px;
	}
	.slider__text {
		margin-bottom: 20px;
	}

	.swiper-pagination {
		position: absolute;
	}
}
@media (orientation: landscape) and (max-width: 1024px) and (max-height: 360px) {
	.slider__promo {
		font-size: 16px;
		margin-bottom: 5px;
		overflow-y: auto;
		max-height: 70px;
	}
	.slider__title {
		max-height: 50px;
		margin-bottom: 5px;
		overflow-y: auto;
		font-size: 20px;
	}
	.slider__text {
		margin-bottom: 5px;
		max-height: 80px;
	}
	.slider__link {
		padding: 10px 30px;
	}
}
@media (max-height: 700+px) {
	.slider__promo {
		overflow-y: auto;
		max-height: 70px;
	}
	.slider__title {
		max-height: 70px;
		margin-bottom: 5px;
		font-size: 20px;
		overflow-y: auto;
	}
	.slider__text {
		margin-bottom: 15px;
		max-height: 150px;
	}
}
@media (max-height: $md4+px) {
	.slider__promo {
		max-height: 30px;
		overflow-y: auto;
	}
	.slider__title {
		max-height: 30px;
		margin-bottom: 5px;
		overflow-y: auto;
		font-size: 20px;
	}
	.slider__text {
		max-height: 80px;
		margin-bottom: 5px;
	}
}
