// Posts
.posts {
	padding: 80px 0;
	&__container {
		max-width: 1200px;
	}

	&__info {
		max-width: 757px;
		margin: 0 auto;
		text-align: center;
		margin-bottom: 40px;
	}

	&__title {
		color: #555;
		margin-bottom: 30px;

		font-family: $fRal;
		font-size: 30px;
		font-weight: 600;
	}

	&__text {
		color: #8c8c8c;

		font-family: $fMon;
		font-size: 16px;
		font-weight: 300;
	}
	@media (max-height: $md3+px) and (orientation: landscape) and (max-width: $md2+px) {
		&__container {
			max-width: 1100px;
			padding: 0 40px;
		}
	}
	@media (max-width: $md3+px) {
		padding: 40px 0;
	}
}
// -- \\

// swiper slider posts
.swiper-posts__wrapper {
	display: flex;
	padding-bottom: 62px;
}
.swiper-posts__slide {
	display: flex;
	flex-direction: column;
}
.swiper-slide__wrapper {
	flex: 1 0 auto;
	display: flex;
	flex-direction: column;
}
.swiper-slide {
	picture {
		width: 100%;
	}
	&__wrapper {
		position: relative;
		display: flex;
		flex-direction: column;

		border: 2px solid #dbdad8;
		width: inherit;
		transition: all 0.3s ease;
	}

	&__slider {
		position: relative;
		flex: 0 0 auto;
		display: block;
		width: 100%;
		img {
			position: relative;

			width: 100%;
			height: 220px;
			object-fit: cover;
			object-position: center;
		}
	}

	&__info {
		flex: 1 0 auto;
		display: flex;
		flex-direction: column;
		padding: 30px;
		text-align: left;
		a {
			flex: 1 0 auto;
			display: flex;
			flex-direction: column;
		}
	}

	&__title {
		flex: 0 0 auto;
		color: #555555;
		font-family: $fRal;

		font-size: 20px;
		font-weight: 500;
		margin-bottom: 20px;

		transition: color 0.3s ease;
	}

	&__text {
		flex: 1 0 auto;
		color: #8c8c8c;
		margin-bottom: 30px;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;

		transition: color 0.3s ease;
		&::-webkit-scrollbar {
			// chrome based
			width: 0px; // ширина scrollbar'a
			background: transparent; // опционально
		}
		-ms-overflow-style: none; // IE 10+
		scrollbar-width: none; // Firefox
	}

	&__link {
		position: relative;

		flex: 0 0 auto;
		display: inline-block;
		padding-right: 15px;
		color: #c7b299;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;

		transition: transform 0.3s ease;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;
			width: 5px;
			height: 8px;
			background: url(../img/posts/arrow.svg) center center no-repeat;
			transform: translateY(-50%);
		}
		&:hover {
			transform: scale(1.1);
		}
		&:focus {
			transform: scale(1.1);
		}
	}

	&__date {
		position: absolute;
		z-index: 10;
		display: flex;
		flex-direction: column;
		flex: 0 0 auto;
		top: 20px;
		left: 20px;
	}

	&__number {
		padding: 8px;
		background-color: #363636;
		border-radius: 3px 3px 0 0;

		font-family: $fMon;
		font-size: 24px;
		font-weight: 300;
		line-height: 20px;

		transition: background-color 0.3s ease;
	}

	&__month {
		border-radius: 0 0 3px 3px;
		background-color: #252525;
		padding: 7px 14px;

		font-family: $fRob;
		font-size: 12px;
		font-weight: 400;

		transition: background-color 0.3s ease;
	}
	@media (min-width: $md3+px) {
		&:hover &__wrapper {
			background-color: #362f2d;
		}
		&:focus &__wrapper {
			background-color: #362f2d;
		}
		&:hover .posts-image-slide {
			&::after {
				opacity: 1;
			}
		}
		&:focus .posts-image-slide {
			&::after {
				opacity: 1;
			}
		}
		&:hover &__title {
			color: #c7b299;
		}
		&:focus &__title {
			color: #c7b299;
		}
		&:hover &__text {
			color: #ffffff;
		}
		&:focus &__text {
			color: #ffffff;
		}
		&:hover &__number {
			background-color: #c7b299;
		}
		&:focus &__number {
			background-color: #c7b299;
		}
		&:hover &__month {
			background-color: #a48d72;
		}
		&:focus &__month {
			background-color: #a48d72;
		}
	}
}

// swiper posts buttons
.swiper-post-button-prev,
.swiper-post-button-next {
	position: absolute;
	bottom: 0;
	width: 22px;
	height: 22px;
	margin-top: calc(-1 * var(--swiper-navigation-size) / 2);
	z-index: 10;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	color: var(--swiper-navigation-color, var(--swiper-theme-color));
	transition: opacity 0.3s ease;
}
.swiper-post-button-prev.swiper-button-disabled,
.swiper-post-button-next.swiper-button-disabled {
	opacity: 0.35;
	cursor: auto;
	pointer-events: none;
}
.swiper-post-button-prev::after,
.swiper-post-button-next::after {
	background: url(../img/posts/arrow-swiper.svg) center center no-repeat;
	width: 22px;
	height: 22px;
}
.swiper-post-button-prev,
.swiper-container-rtl .swiper-post-button-next {
	right: 50%;
	left: auto;
	transform: translateX(-4px);
}
.swiper-post-button-prev::after,
.swiper-container-rtl .swiper-post-button-next:after {
	content: "";

	transform: rotate(180deg);
}
.swiper-post-button-next,
.swiper-container-rtl .swiper-post-button-prev {
	left: 50%;
	right: auto;
	transform: translateX(4px);
}
.swiper-post-button-next::after,
.swiper-container-rtl .swiper-post-button-prev::after {
	content: "";
}
.swiper-post-button-prev.swiper-button-white,
.swiper-post-button-next.swiper-button-white {
	--swiper-navigation-color: #ffffff;
}
.swiper-post-button-prev.swiper-button-black,
.swiper-post-button-next.swiper-button-black {
	--swiper-navigation-color: #000000;
}
.swiper-button-lock {
	display: none;
}
// -- \\
// post-image swiper
.posts-image-slider {
	width: 100%;
}
.posts-image-slide {
	position: relative;
	z-index: 10;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 9;

		width: 100%;
		height: 100%;
		background-color: rgba(17, 17, 17, 0.7);
		opacity: 0;
		transition: opacity 0.3s ease;
	}
}
// -- \\
