.statistics {
	background-image: linear-gradient(to right, #211b19 0%, #4e3427 100%);
	padding-top: 80px;
	padding-bottom: 20px;
	&__container {
		display: flex;
		align-items: flex-start;
		justify-content: space-around;
	}

	&__item {
		flex: 1 1 25%;
		padding-bottom: 90px;
	}
	@media (max-width: $md2+px) {
		padding-top: 30px;
		&__item {
			padding-bottom: 60px;
		}
	}
	@media (max-width: $md3+px) {
		&__container {
			flex-wrap: wrap;
		}
		&__item {
			flex: 1 1 50%;
		}
	}
}

.statistics-item {
	position: relative;

	display: flex;
	flex-direction: column;
	text-align: center;
	&::after {
		content: "";
		display: block;
		position: absolute;
		bottom: 60px;
		left: 50%;

		width: 30px;
		height: 2px;
		background-color: #ffffff;
		transform: translateX(-50%);
	}
	&__image {
		flex-shrink: 0;
		margin-bottom: 30px;
	}

	&__quantity {
		display: block;
		flex: 1 0 auto;
		margin-bottom: 21px;
		color: #fff;

		font-family: $fMon;
		font-size: 48px;
		font-weight: 200;
	}

	&__name {
		display: block;
		flex-shrink: 0;
		color: #c7b299;

		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;
		text-transform: uppercase;
	}
	@media (max-width: $md2+px) {
		&::after {
			bottom: 30px;
		}
		&__quantity {
			font-size: 36px;
		}
	}
}
