.presentasion {
	position: relative;
	padding-top: 160px;
	padding-bottom: 160px;
	background: url(../img/present-bg.jpg) center / cover no-repeat;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		width: 100%;
		height: 100%;

		background-color: rgba(17, 17, 17, 0.5);
	}

	&__container {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		height: 100%;
		z-index: 3;

		text-align: center;
	}

	&__btn {
		display: block;

		height: 100px;
		width: 100px;
		margin-bottom: 44px;

		background: url(../img/video-play.svg) center center no-repeat;
		transition: transform 0.3s ease;
		&:hover {
			transform: scale(1.1);
		}
		&:focus {
			transform: scale(1.1);
		}
	}
	&__title {
		margin-bottom: 30px;

		color: #ffffff;

		font-family: $fRal;
		font-size: 30px;
		font-weight: 500;
		text-align: center;
	}

	&__text {
		margin-bottom: 40px;

		color: #ffffff;

		font-family: $fMon;
		font-size: 16px;
		font-weight: 300;
		line-height: 28px;
		text-align: center;
	}
	&__video-time {
		color: #c7b299;

		font-family: Montserrat;
		font-size: 14px;
		line-height: 24px;
		text-align: center;
	}
	@media (max-width: $md2+px) {
		& {
			background-size: cover;
			padding-top: 100px;
			padding-bottom: 100px;
		}
	}
	@media (max-width: $md2+px) and (orientation: landscape) {
		&__info {
			max-width: 800px;
		}
	}
	@media (max-height: 365px) {
		& {
			height: 100%;
			padding: 20px 0;
		}
	}
	@media (max-width: $md4+px) {
		padding-top: 50px;
		padding-bottom: 50px;
	}
}
