.latest-projects {
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include adaptive-value("padding-top", 80, 20, 1);
		@include adaptive-value("padding-bottom", 80, 20, 1);
	}

	&__info {
		max-width: 757px;
		margin: 0 auto;
		text-align: center;
		margin-bottom: 40px;
		h2 {
			color: #555555;
			font-size: 30px;
			font-weight: 600;
		}
		p {
			color: #8c8c8c;
			font-family: $fMon;
			font-size: 16px;
			font-weight: 300;
			line-height: 28px;
		}
	}

	&__title {
		margin-bottom: 24px;
	}

	&__category {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 30px;
	}

	&__btn {
		border-radius: 3px;
		border: 1px solid #ebebeb;
		background-color: #fff;
		padding: 5px 24px;
		color: #8c8c8c;
		font-family: $fMon;
		font-size: 14px;
		font-weight: 300;
		line-height: 21px;
		text-align: center;
		transition: all 0.2s ease-in;
		margin-bottom: 10px;
		&:not(:last-child) {
			margin-right: 10px;
		}
		&:hover {
			background-color: rgb(197, 191, 186);
			color: #ffffff;
		}
		&:focus {
			background-color: rgb(197, 191, 186);
			color: #ffffff;
		}
	}
	&__btn.active-category {
		background-color: #998675;
		color: #ffffff;
	}
	&__projects {
		display: flex;
		flex-wrap: wrap;
	}
	&__btn-more {
		text-align: center;
	}
	@media (max-width: $md3+px) {
		&__category {
			margin-bottom: 20px;
		}
		&__title {
			margin-bottom: 10px;
		}
		&__info {
			margin-bottom: 20px;
			p {
				font-size: 14px;
			}
		}
		&__btn {
			padding: 5px 10px;
			flex: 0 0 40%;
		}
	}
	@media (max-width: $md4+px) {
		&__btn {
			flex: 0 0 45%;
		}
		&__category {
			margin-bottom: 20px;
		}
		&__projects {
			margin-bottom: 0;
		}
	}
	@media (max-width: $md2+px) and (orientation: landscape) {
		&__wrapper {
			max-width: 830px;
			margin: 0 auto;
		}
	}
}
.project {
	display: none;
	flex-direction: column;
	flex: 0 0 33.33%;
	padding: 0 20px;
	&:not(:last-child) {
		margin-bottom: 40px;
	}
	cursor: pointer;
	&__wrapper {
		display: block;
	}

	&__image {
		position: relative;
		flex: 0 0 auto;
		img {
			display: block;
			width: 100%;
			height: 237px;
			object-position: center;
			object-fit: cover;
		}
	}
	&__image-link {
		display: block;
		&::after {
			content: "";
			position: absolute;
			z-index: 3;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(17, 17, 17, 0);
			transition: all 0.3s ease;
		}
	}

	&__active {
		position: absolute;
		visibility: hidden;
		opacity: 0;
		z-index: 5;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: all 0.3s ease;
	}

	&__copy-btn {
		display: inline-block;
		background: transparent;
		margin-right: 30px;
		border: none;
		&:hover {
			svg {
				fill: #c7b299;
				transition: fill 0.2s ease;
			}
		}
		&:focus {
			svg {
				fill: #c7b299;
				transition: fill 0.2s ease;
			}
		}
		svg {
			fill: #fff;
		}
	}

	&__open {
		&:hover {
			svg {
				fill: #c7b299;
			}
		}
		&:focus {
			svg {
				fill: #c7b299;
			}
		}
		svg {
			fill: #fff;
			transition: fill 0.2s ease;
		}
	}

	&__info {
		position: relative;
		flex: 1 0 auto;

		background-color: #fbfaf8;
		padding: 30px;
		z-index: 5;
		transition: all 0.3s ease;
		h4 {
			color: #c7b299;

			margin-bottom: 14px;
			&::-webkit-scrollbar {
				// chrome based
				width: 0px; // ширина scrollbar'a
				background: transparent; // опционально
			}
			-ms-overflow-style: none; // IE 10+
			scrollbar-width: none; // Firefox

			font-family: $fRal;
			font-size: 20px;
			font-weight: 500;

			transition: all 0.3s ease;
		}
		p {
			color: #d1d1d1;

			&::-webkit-scrollbar {
				// chrome based
				width: 0px; // ширина scrollbar'a
				background: transparent; // опционально
			}
			-ms-overflow-style: none; // IE 10+
			scrollbar-width: none; // Firefox

			font-family: $fMon;
			font-size: 14px;
			font-weight: 300;
			line-height: 20px;

			transition: all 0.3s ease;
		}
		&::after {
			content: "";
			position: absolute;
			top: -30px;
			left: 20px;
			width: 30px;
			height: 30px;
			border: 15px solid transparent;
			border-bottom: 15px solid #fbfaf8;
			background-color: transparent;
			transition: all 0.3s ease;
		}
	}

	&__text-link {
		display: block;
	}

	&:hover .project__image-link::after {
		background-color: rgba(17, 17, 17, 0.7);
	}
	&:focus .project__image-link::after {
		background-color: rgba(17, 17, 17, 0.7);
	}
	&:hover .project__active {
		visibility: visible;
		opacity: 1;
	}
	&:focus .project__active {
		visibility: visible;
		opacity: 1;
	}
	&:hover .project__info {
		background-color: #362f2d;
	}
	&:focus .project__info {
		background-color: #362f2d;
	}
	&:hover .project__info::after {
		border-bottom: 15px solid #362f2d;
	}
	&:focus .project__info::after {
		border-bottom: 15px solid #362f2d;
	}
	&:hover .project__info {
		h4 {
			color: #fff;
		}
		p {
			color: #c7b299;
		}
	}
	&:focus .project__info {
		h4 {
			color: #fff;
		}
		p {
			color: #c7b299;
		}
	}
	@media (max-width: $md2+px) {
		& {
			flex: 0 0 50%;
			padding: 0 10px;
			&:not(:last-child) {
				margin-bottom: 20px;
			}
			//
			.project__image-link::after {
				background-color: rgba(17, 17, 17, 0.7);
			}

			.project__active {
				visibility: visible;
				opacity: 1;
			}

			.project__info {
				background-color: #362f2d;
			}

			.project__info::after {
				border-bottom: 15px solid #362f2d;
			}

			.project__info {
				h4 {
					color: #fff;
				}
				p {
					color: #c7b299;
				}
			}
		}
	}

	@media (max-width: $md4+px) {
		& {
			flex: 0 0 100%;
			padding: 0;
		}
	}
}
.project--onCategory {
	display: flex;
}
// Snackbar
.snackbar {
	font-family: $fMon;
	visibility: hidden; /* Скрыто по умолчанию. Видимый по щелчку мыши */
	min-width: 250px; /* Установите минимальную ширину по умолчанию */
	background-color: #333; /* Черный цвет фона */
	color: #fff; /* Белый цвет текста */
	text-align: center; /* Центрированный текст */
	border-radius: 2px; /* Округленные границы */
	padding: 16px; /* Отступ */
	position: fixed; /* Сядьте на верхнюю часть экрана */
	z-index: 999; /* Добавить z-индекс при необходимости */
	bottom: 30px; /* 30 пикселей снизу */
	left: 100%;
	transform: translate(-110%, 0%);
}

/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
.snackbar.show-snackbar {
	visibility: visible; /* Покажите снэк-бар */
	/* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
	-webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
	animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Анимация, чтобы исчезать снэк-бар в и из */
@-webkit-keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@-webkit-keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}

@keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}
